const bannerInit = document.querySelector(`.banner--init`);
// const bannerWrap = document.querySelector(`.banner__wrap`);
// const bannerTitle = document.querySelector(`.banner__title`);
// const bannerSlogan = document.querySelector(`.banner__slogan`);

const resizeBanner = () => {
  bannerInit.classList.remove(`banner--init`);
  bannerInit.style.minHeight = ``;
  window.removeEventListener(`scroll`, resizeBanner);
  window.removeEventListener(`resize`, resizeBanner);
};

